
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
export default {
components: {
Group,
Cell,
Checklist,
XButton,
Toast
},
data () {
return {




commonList: [ '男', '女'],
msg: 'Hello World!',
radioValue: [],
clientHeight:'',
params:{
'avatar':'',
'nickname':'',
'sex':'',
'sessionId':''
},
showPositionValue: false,
msg: '请选择性别',
middle:'middle'
}
},
mounted(){
const that =this;

this.clientHeight =   `${document.documentElement.clientHeight}`          
console.log(this.clientHeight)
that.changeFixed(this.clientHeight)
},
created () {
this.params.sessionId = getCookie('sessionId')
console.log(this.params.sessionId)
},
methods: {
changeFixed(clientHeight) {                        //动态修改样式
console.log(clientHeight);
this.$refs.homePage.style.height = clientHeight + 'px';

},
change (val, label) {
this.params.sex = val[0]
},
updateUserMsg (e){
const that = this;
Drive.prototype.updateUserMsg(that.params).then(res => {
let list = res.data.data || [];
that.$router.push('../detailmsg')
console.log(list)
});
},
onComfirmTap (e){
console.log(this.params)
if(this.params.sex){
this.updateUserMsg()
}else {
this.showPositionValue = true
}
}
}
}



